import * as React from 'react';
import { CommonLine, MenuItem, SubMenu, InlineMenuItem, TLineStyle, IInlineMenuItemProps } from '@ali/we-design';
import { HrPlugin } from '@ali/4ever-bamboo';
import { SubMenuInnerProps, MenuItemInnerProps } from '@ali/4ever-component';
import { insertHr } from '../actions';
import { DEFAULT_HR_COLOR } from '../constants';

const { hrLines } = HrPlugin;

export const HrMenu = (props: SubMenuInnerProps) => {
  const {
    menuItem,
    controller,
  } = props;

  const handleClick = (key) => {
    const lineType = key.split('_')[1];
    const line = hrLines.find((item) => {
      return item.lineType === lineType;
    });

    if (line) {
      controller.run('onAction', insertHr({ type: line.lineType, color: DEFAULT_HR_COLOR, sz: line.size }));
    }
  };

  const lines = hrLines.map((line) => {
    return (
      <MenuItem
        testid={`hr_${line.lineType}`}
        menuKey={`hr_${line.lineType}`}
        onClick={handleClick}
        key={line.lineType}
      >
        <CommonLine lineType={line.lineType as TLineStyle} length={134} size={line.size} />
      </MenuItem>
    );
  });
  return (
    <SubMenu
      {...menuItem}
      overlayClassName="menu-hr-overlay"
      title={menuItem.title || ''}
      maxHeight="600"
    >
      {lines}
    </SubMenu>
  );
};

export const HrInlineMenu = (props: MenuItemInnerProps) => {
  const { menuItem, controller } = props;
  const { ...rest } = menuItem as IInlineMenuItemProps;

  const handleClick = () => {
    const line = hrLines[0];
    controller.run('onAction', insertHr({ type: line.lineType, color: DEFAULT_HR_COLOR, sz: line.size }));
  };

  return (
    <InlineMenuItem {...rest} onClick={handleClick} />
  );
};
